import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;

import Vue from 'vue'
import App from './App'

// 网络请求封装
import {$http} from "@escook/request-miniprogram"

uni.$http = $http;

$http.baseUrl = "https://www.uinav.com"

$http.beforeRequest = function(options) {
	uni.showLoading({
		title: '数据加载中'
	})
}

$http.afterRequest = function(options) {
	uni.hideLoading();
}

// 提示信息封装
uni.$showMsg = function(title = "数据加载失败", duration = 1500) {
	uni.showToast({
		title,
		duration,
		icon:'none',
	})
}

Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
    ...App
})
app.$mount()